import { join } from 'path';
import { exists, loadJS } from '../utils/fileLoader';
import { Action } from '@sap-ux/vocabularies-types';
import { ExecutionError } from './common';

export type MockEntityContainerContributer = {
    onBeforeAction?(actionDefinition: Action, actionData: any, keys: Record<string, any>): Promise<object>;
    executeAction?(actionDefinition: Action, actionData: any, keys: Record<string, any>): Promise<object>;
    onAfterAction?(
        actionDefinition: Action,
        actionData: any,
        keys: Record<string, any>,
        responseData: any
    ): Promise<any>;
    throwError?(message: string, statusCode?: number, messageData?: object);
};

/**
 *
 */
export class MockEntityContainer {
    /**
     * @param mockDataRootFolder
     * @returns Promise<MockEntityContainerContributer>
     */
    public static async read(mockDataRootFolder: string): Promise<MockEntityContainerContributer> {
        const jsPath = join(mockDataRootFolder, 'EntityContainer') + '.js';
        let outData: MockEntityContainerContributer;
        if (await exists(jsPath)) {
            try {
                //eslint-disable-next-line
                outData = await loadJS(jsPath);
            } catch (e) {
                outData = {};
                console.error(e);
            }
        }
        if (!outData) {
            outData = {
                executeAction: async function (actionDefinition, actionData, keys) {
                    this.throwError('Unsupported Action', 501, {
                        error: {
                            message: `FunctionImport or Action "${actionDefinition.name}" not mocked`
                        }
                    });
                    return [];
                }
            };
        }

        outData.throwError = function (message: string, statusCode = 500, messageData?: object, isSAPMessage = false) {
            const executionError = new ExecutionError(message, statusCode, messageData, isSAPMessage);
            throw executionError;
        };

        return outData;
    }
}
