import { Request, Response } from 'express';
import Router from 'router';
import { getLogger } from '@ui5/logger';
import { oDataHandler } from './router/odata';
import { ServerConfig } from './api';
import { catalogService } from './router/catalog';

import { isInBrowser, loadFile } from './router/utils/fileLoader';
import { mockAnswer } from './browserMiddleware';
import { resolveConfig } from './configResolver';

/**
 * @param req
 * @param res
 * @param next
 */
function disableCache(req, res, next): void {
    res.setHeader('Cache-Control', 'private, no-cache, no-store, must-revalidate');
    res.setHeader('Expires', '-1');
    res.setHeader('Pragma', 'no-cache');
    next();
}

function escapeRegex(string) {
    return string.replace(/[-\\^$*+?.()|[\]{}]/g, '\\$&');
}

/**
 * @param str
 */
function encode(str) {
    return str.replace(/'/g, '%27');
}

/**
 * @param inConfig
 */
export async function createMockMiddleware(inConfig: ServerConfig, basePath?: string): Promise<Router> {
    const app: Router = Router();
    const log = getLogger('server:ux-fe-mockserver');
    app.use(disableCache);

    const newConfig = resolveConfig(inConfig, basePath);
    if (newConfig.watch) {
        log.info(`Mockserver is running in watch mode`);
    }
    const oDataHandlerPromises = newConfig.services.map(async (mockService) => {
        try {
            const oDataHandlerInstance = await oDataHandler(mockService);
            if (newConfig.contextBasedIsolation || mockService.contextBasedIsolation) {
                const subRouter: Router = Router();
                subRouter.use(`${mockService.urlPath}`, oDataHandlerInstance);
                subRouter.use(`${encode(mockService.urlPath)}`, oDataHandlerInstance);
                app.use(/^\/tenant-(\d{1,3})/, subRouter);
            }
            if (newConfig.debug || mockService.debug) {
                log.info(`Mockdata location: ${mockService.mockdataRootPath}`);
                log.info(`Service path: ${mockService.urlPath}`);
            }
            app.use(`${mockService.urlPath}`, oDataHandlerInstance);
            app.use(`${encode(mockService.urlPath)}`, oDataHandlerInstance);
        } catch (e) {
            if (newConfig.debug || mockService.debug) {
                console.error(e);
            }
            console.log('Failed to start ' + mockService.urlPath);
        }
    });
    // Prepare the catalog service
    app.use('/sap/opu/odata/IWFND/CATALOGSERVICE;v=2', catalogService(newConfig.services));
    // Prepare the annotation files
    newConfig.annotations.map(async (mockAnnotation) => {
        app.get(escapeRegex(mockAnnotation.urlPath), (req: Request, res: Response) => {
            loadFile(mockAnnotation.localPath)
                .then((data) => {
                    res.setHeader('Content-Type', 'application/xml');
                    res.write(data);
                    res.end();
                })
                .catch((error) => {
                    console.error(error);
                });
        });
    });

    const serverInit = Promise.all(oDataHandlerPromises);
    await serverInit;

    if (isInBrowser()) {
        // eslint-disable-next-line @typescript-eslint/no-var-requires
        const nise = require('nise');
        const fakeServer = nise.fakeServer;
        const escapePath = function (sPath) {
            // eslint-disable-next-line
            return sPath.replace(/[\\\/\[\]\{\}\(\)\-\*\+\?\.\^\$\|]/g, '\\$&');
        };

        nise.fakeXhr.FakeXMLHttpRequest.useFilters = true;

        const mockServer = fakeServer.create();
        mockServer.autoRespond = true;
        newConfig.services.map((newService) => {
            const serverhook = new RegExp(escapePath(newService.urlPath) + '.*');
            nise.fakeXhr.FakeXMLHttpRequest.addFilter((method, url /*async, username, password*/) => {
                return !serverhook.test(url);
            });
            mockServer.respondWith(serverhook, mockAnswer(app));
        });

        app.mockServer = mockServer;
    }

    app.readyPromise = serverInit;

    return app;
}
