import { ODataRequest, DataQuery } from './odataRequest';
import { SimpleResponse } from '../batch/http/simpleHttpResponse';

/**
 *
 */
export class ODataV2Request extends ODataRequest {
    /**
     * @param expandParameters
     */
    protected parseExpand(expandParameters): Partial<DataQuery> {
        const props = expandParameters.split(',');
        return props.reduce(
            (reducer, property) => {
                reducer.expand[property] = {
                    select: { '*': true },
                    expand: {}
                };
                reducer.properties[property.split('/')[0]] = true;
                return reducer;
            },
            { expand: {}, properties: {} }
        );
    }

    // protected enrichElement(entity: string, element: object): void {
    //     const keyValues: string[] = [];
    //     const keys = this.metadata.getKeys(entity);
    //     keys.forEach((key) => {
    //         keyValues.push(`${key}='${element[key]}'`);
    //     });
    //     const uri = `${this.baseUrl}/${entity}(${keyValues.join(',')})`;
    //     element['__metadata'] = {
    //         id: uri,
    //         uri: uri,
    //         type: this.metadata.getEntitySet(this.query.entitySet).EntityType
    //     };
    // }

    /**
     * @param data
     * @param bCreated
     */
    public createResponse(data: any, bCreated = false): SimpleResponse {
        const response = super.createResponse(data);
        response.setHeader('Content-Type', 'application/json');
        response.setHeader('dataserviceversion', '2.0');
        response.setHeader('sap-metadata-last-modified', 'Tue, 01 Oct 2019 15:02:44 GMT');
        response.setHeader('cache-control', 'no-store, no-cache');
        if (data && data.__metadata && data.__metadata.id) {
            response.setHeader('location', data.__metadata.id);
        }

        return response;
    }

    /**
     * @param data
     */
    protected toJSON(data: object | object[]): string {
        if ((data as object[]).length !== undefined) {
            return JSON.stringify({
                d: {
                    __count: (data as object[]).length + '',
                    results: data
                }
            });
        } else {
            return JSON.stringify({ d: data || {} });
        }
    }
}
