import * as http from 'http';

/**
 *
 */
export class SimpleResponse {
    statusCode: number = null;
    headers: object = {};
    data = '';

    /**
     * @param code
     * @param headers
     */
    public writeHead(code, headers): void {
        this.statusCode = code;
        Object.assign(this.headers, headers);
    }

    /**
     * @param name
     * @param value
     */
    public setHeader(name, value): void {
        this.headers[name] = value;
    }

    /**
     * @param code
     */
    public status(code): SimpleResponse {
        this.statusCode = code;
        return this;
    }

    /**
     * @param data
     */
    public write(data): SimpleResponse {
        this.data += data;
        return this;
    }

    public end(): void {
        // nothing to do
    }

    /**
     * @param response
     */
    public writeToBatchResponse(response /*, contentId*/): void {
        let value;
        const headers = this.headers;
        response.write('HTTP/1.1 ' + this.statusCode + ' ' + http.STATUS_CODES['' + this.statusCode] + '\r\n');

        Object.keys(headers).forEach(function (key) {
            value = headers[key];
            response.write(key + ': ' + value + '\r\n');
        });

        response.write('\r\n'); // end of header
        response.write(this.data);
        if (this.data.length > 0) {
            response.write('\r\n'); //as used in OData_Specification_V2.0 batch samples
        }
    }

    /**
     * @param res
     */
    public writeToResponse(res): void {
        res.statusCode = this.statusCode;
        Object.keys(this.headers).forEach((headerName) => {
            res.setHeader(headerName, this.headers[headerName]);
        });
        res.send(this.data);
    }
}

/**
 *
 */
export function createResponse(): SimpleResponse {
    return new SimpleResponse();
}
