import { Request, Response } from 'express';
import Router from 'router';
import { ServiceConfig } from '../api';

/**
 * @param servicesConfig
 * @param version
 */
export function catalogService(servicesConfig: ServiceConfig[], version = 2): Router {
    const router: Router = Router();

    router.get('/ServiceCollection', (req: Request, res: Response) => {
        res.json({
            d: {
                results: servicesConfig.map((serviceConfig) => {
                    return {
                        ID: serviceConfig._internalName
                    };
                })
            }
        });
    });

    router.get('/ServiceCollection\\(*', (req: Request, res: Response) => {
        res.json({
            d: {
                results: servicesConfig.map((serviceConfig) => {
                    return {
                        TechnicalName: serviceConfig._internalName,
                        Version: version
                    };
                })
            }
        });
    });

    return router;
}
