import { readFileSync, exists as fsExists, readFile } from 'graceful-fs';
import { promisify } from 'util';
import path from 'path';
let isBrowser = false;
let readFileP;
let existsP;
if (!readFileSync) {
    isBrowser = true;
} else {
    readFileP = promisify(readFile);
    existsP = promisify(fsExists);
}

/**
 *
 */
export function isInBrowser(): boolean {
    return isBrowser;
}

/**
 * @param filePath
 */
export async function loadFile(filePath: string): Promise<string> {
    if (!isBrowser) {
        return readFileP(filePath, 'utf-8');
    } else {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        filePath = sap.ui.loader._.resolveURL(sap.ui.loader._.toUrl(filePath));
        return fetch(filePath).then((res) => {
            return res.text();
        });
    }
}

/**
 * @param filePath
 */
export async function exists(filePath: string): Promise<boolean> {
    if (!isBrowser) {
        return existsP(filePath);
    } else {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        filePath = sap.ui.loader._.resolveURL(sap.ui.loader._.toUrl(filePath));
        return fetch(filePath, { method: 'HEAD' }).then((res) => {
            return res.status === 200;
        });
    }
}

/**
 * @param filePath
 */
export async function loadJS(filePath: string) {
    if (!isBrowser) {
        delete require.cache[filePath];
        // eslint-disable-next-line @typescript-eslint/no-var-requires
        return Promise.resolve(require(/* webpackIgnore: true */ filePath));
    } else {
        return new Promise((resolve, reject) => {
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            sap.ui.require([path.dirname(filePath) + '/' + path.basename(filePath, '.js')], function (fileContent) {
                resolve(fileContent);
            });
        });
    }
}

/**
 * @param basePath
 * @param targetPath
 */
export function resolvePath(basePath: string, targetPath: string) {
    if (!isBrowser) {
        return path.resolve(basePath, targetPath);
    } else {
        return targetPath;
    }
}
