/**
 *
 */
export class AppHttp {
    type: string;
    rawData: object;
    contentId: any;
    request: any;
    response: any;

    /**
     * @param {string} url
     * @param {Object} headers
     * @param {Array} payload
     * @class
     */
    public constructor(url, headers, payload) {
        this.type = 'app';
        this.rawData = {
            url: url,
            headers: headers,
            payload: payload
        };

        this.contentId = undefined;
        this.request = undefined;
        this.response = undefined;
    }

    /**
     * @param context
     * @param response
     */
    public write(context, response) {
        response.write('Content-Type: application/http\r\n');
        response.write('Content-Transfer-Encoding: binary\r\n');
        if (this.contentId) {
            response.write('Content-ID: ' + this.contentId.id + '\r\n');
        }
        response.write('\r\n'); //end of header

        this.response.writeToBatchResponse(response, this.contentId);
    }
}
