import { NextFunction, Request } from 'express';
import Router from 'router';
import { getLogger } from '@ui5/logger';
import { ServiceConfig } from '../api';
import { batchRequestHandler } from './batch';
import { SimpleResponse } from './batch/http/simpleHttpResponse';
import { ODataV2Request } from './request/odataV2Request';
import { ODataMetadata } from './data/metadata';
import { DataAccess } from './data/dataAccess';
import { ODataV4Request } from './request/odataV4Request';
import { ODataRequest } from './request/odataRequest';
import { isInBrowser, loadFile } from './utils/fileLoader';
import { createRequest } from './batch/http/simpleHttpRequest';
import * as http from 'http';
import etag from 'etag';
/**
 * @param service
 */
export async function oDataHandler(service: ServiceConfig): Promise<Router> {
    const router: Router = Router();
    const log = getLogger('server:ux-fe-mockserver');
    let edmx;
    if (service.metadataCdsPath) {
        const cdsContent = await loadFile(service.metadataCdsPath);
        const stringToCds = await import('./utils/stringToCds');

        edmx = await stringToCds.compileCDS(cdsContent, service.cdsServiceName);
    } else {
        edmx = await loadFile(service.metadataXmlPath);
    }

    let metadata = await ODataMetadata.parse(edmx);
    const dataAccess = new DataAccess(
        service.mockdataRootPath,
        metadata,
        !!service.strictKeyMode,
        !!service.contextBasedIsolation,
        !!service.generateMockData,
        !!service.debug
    );

    if (service.watch && !isInBrowser()) {
        const watchPath = [service.mockdataRootPath];
        if (service.metadataXmlPath) {
            watchPath.push(service.metadataXmlPath);
        }
        if (service.metadataCdsPath) {
            watchPath.push(service.metadataCdsPath);
        }
        const chokidar = await import('chokidar');
        chokidar
            .watch(watchPath, {
                ignoreInitial: true
            })
            .on('all', async function (event, path) {
                log.info(`Change detected for service ${service.urlPath}... restarting`);
                if (service.debug) {
                    log.info(`${event} on ${path}`);
                }
                if (service.metadataCdsPath) {
                    const cdsContent = await loadFile(service.metadataCdsPath);
                    const stringToCds = await import('./utils/stringToCds');

                    edmx = stringToCds.compileCDS(cdsContent, service.cdsServiceName);
                } else {
                    edmx = await loadFile(service.metadataXmlPath);
                }
                metadata = await ODataMetadata.parse(edmx);
                dataAccess.reloadData(metadata);
                log.info(`Service ${service.urlPath} restarted`);
            });
    }

    router.post('/\\$metadata/reload', (req: Request, res: http.ServerResponse) => {
        dataAccess.reloadData(metadata);
        res.setHeader('Content-Type', 'application/json');
        res.write(JSON.stringify({ message: 'Reload success' }));
        res.end();
    });
    router.get('/\\$metadata', (req: Request, res: http.ServerResponse) => {
        res.setHeader('Content-Type', 'application/xml');
        if (!isInBrowser() && !service.noETag) {
            res.setHeader('ETag', etag(edmx, { weak: true }));
        }

        res.write(edmx);
        res.end();
    });

    router.use('/\\$batch', batchRequestHandler(metadata, dataAccess, service));

    router.get('/', (req: Request, res: http.ServerResponse) => {
        const data = `<?xml version="1.0" encoding="utf-8"?>
        <app:service xml:lang="en" xml:base="${service.urlPath}/"
            xmlns:app="http://www.w3.org/2007/app"
            xmlns:atom="http://www.w3.org/2005/Atom"
            xmlns:m="http://schemas.microsoft.com/ado/2007/08/dataservices/metadata"
            xmlns:sap="http://www.sap.com/Protocols/SAPData">
            <app:workspace>
            </app:workspace>
            <atom:link rel="self" href="${service.urlPath}/"/>
            <atom:link rel="latest-version" href="${service.urlPath}/"/>
        </app:service>`;
        res.setHeader('Content-Type', 'application/xml');
        res.write(data);
        res.end();
    });
    router.head('/*', (req: Request, res: http.ServerResponse) => {
        const tenantId = req.originalUrl.indexOf('/tenant-') === 0 ? req.originalUrl.split('/')[1] : 'tenant-default';
        res.setHeader('sap-tenantId', tenantId);
        const contextId = req.header('sap-contextid');
        if (contextId) {
            dataAccess.resetStickySessionTimeout(res, tenantId);
        }
    });

    router.get('/*', async (req: Request, res: http.ServerResponse, next: NextFunction) => {
        try {
            const odataReq: ODataRequest =
                metadata.getVersion() === '2.0'
                    ? new ODataV2Request(req.baseUrl, req.url.substring(1), metadata)
                    : new ODataV4Request(req.baseUrl, req.url.substring(1), metadata);

            res.setHeader('sap-tenantId', odataReq.tenantId);
            res.setHeader('Content-Type', 'application/json');
            const actionResponse = await dataAccess.performAction(odataReq);
            let response;
            if (actionResponse === null) {
                const data = await dataAccess.getData(odataReq);
                response = odataReq.createResponse(data);
            } else {
                response = odataReq.createResponse(actionResponse);
            }
            response.writeToResponse(res);
        } catch (e) {
            next(ODataRequest.createErrorResponse(e));
        }
    });

    router.post('/*', async (req: Request, res: http.ServerResponse, next: NextFunction) => {
        try {
            const odataReq: ODataRequest =
                metadata.getVersion() === '2.0'
                    ? new ODataV2Request(req.baseUrl, req.url.substring(1), metadata)
                    : new ODataV4Request(req.baseUrl, req.url.substring(1), metadata);

            res.setHeader('sap-tenantId', odataReq.tenantId);
            res.setHeader('Content-Type', 'application/json');

            const request = createRequest(req, '/');
            request.getBodyAsString(async (body) => {
                const postData = body ? JSON.parse(body) : {};
                const actionResponse = await dataAccess.performAction(odataReq, postData);
                let response;
                if (actionResponse === null) {
                    const createdData = await dataAccess.createData(odataReq, postData);
                    response = odataReq.createResponse(createdData, true, createdData);
                } else {
                    response = odataReq.createResponse(actionResponse);
                }
                response.writeToResponse(res);
            });
        } catch (e) {
            next(ODataRequest.createErrorResponse(e));
        }
    });

    router.patch('/*', async (req: Request, res: http.ServerResponse, next: NextFunction) => {
        try {
            const odataReq: ODataRequest =
                metadata.getVersion() === '2.0'
                    ? new ODataV2Request(req.baseUrl, req.url.substring(1), metadata)
                    : new ODataV4Request(req.baseUrl, req.url.substring(1), metadata);

            res.setHeader('sap-tenantId', odataReq.tenantId);
            res.setHeader('Content-Type', 'application/json');

            const request = createRequest(req, '/');
            request.getBodyAsString(async (body) => {
                try {
                    const data = await dataAccess.updateData(odataReq, JSON.parse(body));
                    const response = odataReq.createResponse(data);
                    response.writeToResponse(res);
                } catch (e) {
                    next(ODataRequest.createErrorResponse(e));
                }
            });
        } catch (e) {
            next(ODataRequest.createErrorResponse(e));
        }
    });

    router.use('*', (err: any, req: Request, res: http.ServerResponse, next: NextFunction) => {
        if (res.headersSent) {
            return next(err);
        }

        if (err instanceof SimpleResponse) {
            err.writeToResponse(res);
        } else {
            return next(err);
        }
    });

    return router;
}
