/**
 *
 */
export class Batch {
    type: any;
    isChangeSet: boolean;
    changeSetErrorResponse: any;
    parts: any;

    /**
     * @param type
     */
    public constructor(type) {
        this.type = type;
        this.isChangeSet = type === 'changeset';
        if (this.isChangeSet) {
            this.changeSetErrorResponse = null;
        }
        this.parts = [];
    }
}
