import url from 'url';

export function mockAnswer(app) {
    return async function (xhr /*, id*/) {
        const parsedUrl = url.parse(xhr.url);
        const req = {
            url: parsedUrl.href,
            method: xhr.method,
            body: xhr.requestBody,
            headers: xhr.requestHeaders
        };
        if (req.headers['Content-Type'] === 'multipart/mixed;charset=utf-8') {
            const oBoundaryRegex = new RegExp('batch_[a-z0-9-]*');
            const sBoundary = oBoundaryRegex.exec(req.body)[0];
            req.headers['Content-Type'] = 'multipart/mixed; boundary=' + sBoundary;
        }
        Object.keys(req.headers).forEach((headerText) => {
            req.headers[headerText.toLowerCase()] = req.headers[headerText];
        });
        const responseHeaders = {};
        let responseStatusCode = 200;
        let responseContentType;
        let responseBuffer = '';
        const res = {
            header: (headerName, headerValue) => {
                //responseHeaders[headerName] = headerValue;
                responseHeaders[headerName.toLowerCase()] = headerValue;
            },
            getHeader: (headerName) => {
                return responseHeaders[headerName];
            },
            setHeader: (headerName, headerValue) => {
                res.header(headerName, headerValue);
            },
            status: (statusCode) => {
                responseStatusCode = statusCode;
            },
            type: (contentType) => {
                responseContentType = contentType;
                return res;
            },
            contentType: (contentType) => {
                responseContentType = contentType;
                res.header('Content-Type', responseContentType);
                return res;
            },
            write: (data: string) => {
                responseBuffer += data;
            },
            end: () => {
                res.send(responseBuffer);
            },
            send: (data: string) => {
                xhr.readyState = 1;
                if (typeof data === 'object') {
                    data = JSON.stringify(data);
                }
                xhr.respond(responseStatusCode, responseHeaders, data);
            }
        };
        xhr.readyState = 4;
        app(req, res, function (arg1 /*arg2, arg3*/) {
            console.error(arg1);
        });
    };
}
