import cloneDeep from 'lodash.clonedeep';
import { ComplexType, Property, EntityType } from '@sap-ux/annotation-converter';
import { generateId, uuidv4 } from './id';
import { Action } from '@sap-ux/vocabularies-types';
import { EntitySetInterface } from './common';

export type KeyDefinitions = Record<string, number | boolean | string>;

/**
 *
 */
export class FileBasedMockData {
    protected _mockData: object[];
    protected _entityType: EntityType;
    protected _mockDataEntitySet: EntitySetInterface;
    protected _contextId: string;
    /**
     * @param mockData
     * @param entityType
     * @param mockDataEntitySet
     */
    constructor(mockData: object[], entityType: EntityType, mockDataEntitySet: EntitySetInterface, contextId: string) {
        this._entityType = entityType;
        this._contextId = contextId;

        this._mockDataEntitySet = mockDataEntitySet;
        if (mockData.length === 0 && (mockData as any).__generateMockData) {
            this._mockData = this.generateMockData();
        } else {
            this._mockData = cloneDeep(mockData);
            if (this._mockData.forEach) {
                this._mockData.forEach((mockLine) => {
                    // We need to ensure that complex types are at least partially created
                    this._entityType.entityProperties.forEach((prop) => {
                        if (prop.targetType && !mockLine[prop.name]) {
                            mockLine[prop.name] = {};
                            prop.targetType.properties.forEach((subProp) => {
                                mockLine[prop.name][subProp.name] = this.getDefaultValueFromType(
                                    subProp.type,
                                    subProp.targetType,
                                    subProp.defaultValue
                                );
                            });
                        }
                    });
                });
            }
        }
    }

    /**
     * @param mockEntry
     */
    addEntry(mockEntry): void {
        this._mockData.push(mockEntry);
    }

    /**
     * @param keyValues
     * @param updatedData
     */
    updateEntry(keyValues: KeyDefinitions, updatedData: object): void {
        const dataIndex = this.getDataIndex(keyValues);
        this._mockData[dataIndex] = updatedData;
    }

    /**
     * @param keyValues
     */
    fetchEntries(keyValues: KeyDefinitions): object[] {
        const keys = this._entityType.keys;
        return this._mockData.filter((mockData) => {
            return Object.keys(keyValues).every((keyName) => {
                return this._mockDataEntitySet.checkKeyValue(
                    mockData,
                    keyValues,
                    keyName,
                    keys.find((keyProp) => keyProp.name === keyName) as Property
                );
            });
        });
    }

    /**
     * @param keyValues
     */
    hasEntry(keyValues: KeyDefinitions): boolean {
        return this.getDataIndex(keyValues) !== -1;
    }

    /**
     *
     */
    hasEntries(): boolean {
        return this._mockData.length > 0;
    }

    /**
     *
     */
    getAllEntries(dontClone: boolean = false): object[] {
        if (dontClone) {
            return this._mockData;
        }
        return cloneDeep(this._mockData);
    }

    /**
     * @param keyValues
     */
    protected getDataIndex(keyValues: KeyDefinitions): number {
        const keys = this._entityType.keys;
        return this._mockData.findIndex((mockData) => {
            return Object.keys(keyValues).every((keyName) => {
                return this._mockDataEntitySet.checkKeyValue(
                    mockData,
                    keyValues,
                    keyName,
                    keys.find((keyProp) => keyProp.name === keyName) as Property
                );
            });
        });
    }

    /**
     * @param keyValues
     */
    removeEntry(keyValues: KeyDefinitions): void {
        const dataIndex = this.getDataIndex(keyValues);
        if (dataIndex !== -1) {
            this._mockData.splice(dataIndex, 1);
        }
    }

    /**
     * @param type
     * @param complexType
     * @param defaultValue
     */
    protected getDefaultValueFromType(type: string, complexType: ComplexType, defaultValue?: any): any {
        if (complexType) {
            const outData = {};
            complexType.properties.forEach((subProp) => {
                outData[subProp.name] = this.getDefaultValueFromType(
                    subProp.type,
                    subProp.targetType,
                    subProp.defaultValue
                );
            });
            return outData;
        } else {
            if (defaultValue !== undefined) {
                return defaultValue;
            }
            switch (type) {
                case 'Edm.Int16':
                case 'Edm.Byte':
                case 'Edm.Int32':
                case 'Edm.Int64':
                    return 0;
                case 'Edm.Boolean':
                    return false;
                case 'Edm.DateTimeOffset': {
                    const date = new Date();
                    return this._mockDataEntitySet.isV4() ? date.toISOString() : '/Date(' + date.getTime() + '+0000)/';
                }
                case 'Edm.Date':
                case 'Edm.DateTime': {
                    const date = new Date();
                    const dateOut =
                        date.getUTCFullYear() +
                        '-' +
                        ('0' + (date.getUTCMonth() + 1)).slice(-2) +
                        '-' +
                        ('0' + date.getUTCDate()).slice(-2);
                    return this._mockDataEntitySet.isV4() ? dateOut : '/Date(' + date.getTime() + '+0000)/';
                }
                case 'Edm.Time':
                case 'Time': {
                    const date = new Date();
                    // ODataModel expects ISO8601 duration format
                    return 'PT' + date.getHours() + 'H' + date.getMinutes() + 'M' + date.getSeconds() + 'S';
                }
                default:
                    return '';
            }
        }
    }

    /**
     * @param type
     * @param complexType
     * @param propertyName
     * @param lineIndex
     */
    protected getRandomValueFromType(
        type: string,
        complexType: ComplexType,
        propertyName: string,
        lineIndex: number
    ): any {
        if (complexType) {
            const outData = {};
            complexType.properties.forEach((subProp) => {
                outData[subProp.name] = this.getRandomValueFromType(
                    subProp.type,
                    subProp.targetType,
                    subProp.name,
                    lineIndex
                );
            });
            return outData;
        } else {
            switch (type) {
                case 'Edm.Int16':
                case 'Edm.Int32':
                case 'Edm.Int64':
                    return Math.floor(Math.random() * 10000);
                case 'Edm.String':
                    return `${propertyName}_${lineIndex}`;
                case 'Edm.Boolean':
                    return Math.random() < 0.5;
                case 'Edm.Byte':
                    return Math.floor(Math.random() * 10);
                case 'Edm.Decimal':
                    return Math.floor(Math.random() * 100000) / 100;
                case 'Edm.Guid':
                    return uuidv4();
                case 'Edm.Date':
                case 'Edm.DateTime':
                case 'Edm.DateTimeOffset': {
                    const date = new Date();
                    date.setFullYear(2000 + Math.floor(Math.random() * 22));
                    date.setDate(Math.floor(Math.random() * 30));
                    date.setMonth(Math.floor(Math.random() * 12));
                    date.setMilliseconds(0);
                    if (type === 'Edm.Date') {
                        const dateOut =
                            date.getUTCFullYear() +
                            '-' +
                            ('0' + (date.getUTCMonth() + 1)).slice(-2) +
                            '-' +
                            ('0' + date.getUTCDate()).slice(-2);
                        return this._mockDataEntitySet.isV4() ? dateOut : '/Date(' + date.getTime() + '+0000)/';
                    } else {
                        return this._mockDataEntitySet.isV4()
                            ? date.toISOString()
                            : '/Date(' + date.getTime() + '+0000)/';
                    }
                }
                case 'Edm.Time':
                case 'Time':
                    // ODataModel expects ISO8601 duration format
                    return (
                        'PT' +
                        Math.floor(Math.random() * 23) +
                        'H' +
                        Math.floor(Math.random() * 59) +
                        'M' +
                        Math.floor(Math.random() * 59) +
                        'S'
                    );
                case 'Edm.TimeOfDay':
                case 'Edm.Binary':
                default:
                    return '';
            }
        }
    }

    /**
     *
     */
    getEmptyObject(): object {
        const outObj = {};
        this._entityType.entityProperties.forEach((property) => {
            outObj[property.name] = this.getDefaultValueFromType(
                property.type,
                property.targetType,
                property.defaultValue
            );
        });

        return outObj;
    }

    /**
     *
     */
    getDefaultElement(): object {
        if (this._mockData && !this._mockData.length) {
            return this._mockData;
        } else if (this._mockData.length >= 1) {
            return cloneDeep(this._mockData[0]);
        } else {
            return this.getEmptyObject();
        }
    }

    /**
     * @param property
     * @param lineIndex
     */
    generateKey(property: Property, lineIndex?: number, mockData: any = []) {
        const currentMockData = this._mockData || mockData;
        let highestIndex;
        switch (property.type) {
            case 'Edm.Int32':
                highestIndex = 0;
                currentMockData.forEach((mockLine: any) => {
                    const lineIndex = parseInt(mockLine[property.name], 10);
                    highestIndex = Math.max(highestIndex, lineIndex);
                });
                return highestIndex + 1;
            case 'Edm.Boolean':
                return Math.random() > 0.5;
            case 'Edm.Guid':
                return uuidv4();
            case 'Edm.String':
                if (lineIndex === undefined) {
                    lineIndex = currentMockData.length + 1;
                }
                return `${property.name}_${lineIndex}`;
            default:
                return generateId(12);
        }
    }

    /**
     * @param iIndex
     */
    generateMockDataLine(iIndex: number, mockData: any) {
        const outObj = {};
        this._entityType.entityProperties.forEach((property) => {
            if (property.isKey) {
                outObj[property.name] = this.generateKey(property, iIndex, mockData);
            } else {
                outObj[property.name] = this.getRandomValueFromType(
                    property.type,
                    property.targetType,
                    property.name,
                    iIndex
                );
            }
        });

        return outObj;
    }

    getParentEntityInterface(): Promise<FileBasedMockData | undefined> {
        return this._mockDataEntitySet.getParentEntityInterface(this._contextId);
    }

    getEntityInterface(entitySetName: string): Promise<FileBasedMockData | undefined> {
        return this._mockDataEntitySet.getEntityInterface(entitySetName, this._contextId);
    }

    /**
     *
     */
    generateMockData() {
        const mockData = [];
        for (let i = 0; i < 150; i++) {
            mockData.push(this.generateMockDataLine(i, mockData));
        }
        return mockData;
    }

    /**
     * Allow to modify the action data beforehand
     *
     * @param actionDefinition
     * @param actionData
     * @param keys
     */
    async onBeforeAction(actionDefinition: Action, actionData: any, keys: Record<string, any>): Promise<object> {
        return actionData;
    }
    /**
     * Do something with the action
     *
     * @param actionDefinition
     * @param actionData
     * @param keys
     */
    async executeAction(actionDefinition: Action, actionData: any, keys: Record<string, any>): Promise<object> {
        return actionData;
    }

    /**
     * Allow to modify the response data
     *
     * @param actionDefinition
     * @param actionData
     * @param keys
     * @param responseData
     */
    async onAfterAction(
        actionDefinition: Action,
        actionData: any,
        keys: Record<string, any>,
        responseData: any
    ): Promise<any> {
        return responseData;
    }

    //eslint-disable-next-line
    async onAfterUpdateEntry(keyValues: KeyDefinitions, updatedData: object): Promise<void> {}
    //eslint-disable-next-line
    async onBeforeUpdateEntry(keyValues: KeyDefinitions, updatedData: object): Promise<void> {}
    //eslint-disable-next-line
    hasCustomAggregate(customAggregateName: string): boolean {
        return false;
    }
    //eslint-disable-next-line
    performCustomAggregate(customAggregateName: string, dataToAggregate: any[]): any {}
}
