import { ODataRequest } from '../request/odataRequest';
import { ODataMetadata } from './metadata';
import { MockDataEntitySet } from './entitySet';
import { DraftMockEntitySet } from './draftEntitySet';
import { StickyMockEntitySet } from './stickyEntitySet';
import { EntitySet, EntityType } from '@sap-ux/annotation-converter';
import { ODataV4Request } from '../request/odataV4Request';
import cloneDeep from 'lodash.clonedeep';
import { getLogger } from '@ui5/logger';
import { ContainedDataEntitySet } from './ContainedDataEntitySet';
import { DataAccessInterface, _getDateTimeOffset } from './common';
import { MockEntityContainer } from './mockEntityContainer';
import { KeyDefinitions } from './fileBasedMockData';

/**
 *
 */
export class DataAccess implements DataAccessInterface {
    protected readonly mockDataRootFolder: string;
    protected metadata: ODataMetadata;
    public debug: boolean;
    public log;
    protected readonly strictKeyMode: boolean;
    protected readonly contextBasedIsolation: boolean;
    protected entitySets: Record<string, MockDataEntitySet> = {};
    protected stickyEntitySets: StickyMockEntitySet[] = [];
    protected generateMockData: boolean;
    /**
     * @param root
     * @param metadata
     * @param strictKeyMode
     * @param contextBasedIsolation
     * @param generateMockData
     */
    public constructor(
        root: string,
        metadata: ODataMetadata,
        strictKeyMode: boolean,
        contextBasedIsolation: boolean,
        generateMockData: boolean,
        debugMode: boolean = false
    ) {
        this.mockDataRootFolder = root;
        this.metadata = metadata;
        this.debug = debugMode;
        this.log = getLogger('server:ux-fe-mockserver');
        this.strictKeyMode = strictKeyMode;
        this.generateMockData = generateMockData;
        this.contextBasedIsolation = contextBasedIsolation;
        this.metadata.getEntitySets().forEach((entitySet) => {
            this.getMockEntitySet(entitySet.name, generateMockData);
        });
    }

    public reloadData(metadata: ODataMetadata) {
        this.metadata = metadata;
        this.entitySets = {};

        this.metadata.getEntitySets().forEach((entitySet) => {
            this.getMockEntitySet(entitySet.name, this.generateMockData);
        });
    }

    /**
     *
     */
    public isV4(): boolean {
        return this.metadata.getVersion() !== '2.0';
    }

    /**
     * @param entityTypeName
     * @param generateMockData
     * @param containedEntityType
     * @param containedData
     */
    public async getMockEntitySet(
        entityTypeName,
        generateMockData?: boolean,
        containedEntityType?: EntityType,
        containedData?: any
    ): Promise<MockDataEntitySet> {
        if (containedEntityType) {
            const mockEntitySet = new ContainedDataEntitySet(containedEntityType, containedData, this);
            return mockEntitySet.readyPromise;
        } else if (!this.entitySets[entityTypeName]) {
            const entitySet = this.metadata.getEntitySet(entityTypeName);
            const entityType = this.metadata.getEntityType(entityTypeName);
            let mockEntitySet: MockDataEntitySet;
            if (this.metadata.isDraftEntity(entitySet)) {
                if (this.debug) {
                    this.log.info('Creating draft entity for ' + entitySet?.name);
                }
                mockEntitySet = new DraftMockEntitySet(
                    this.mockDataRootFolder,
                    entitySet || entityType,
                    this,
                    generateMockData
                );
            } else if (this.metadata.isStickyEntity(entitySet)) {
                if (this.debug) {
                    this.log.info('Creating sticky entity for ' + entitySet?.name);
                }
                mockEntitySet = new StickyMockEntitySet(
                    this.mockDataRootFolder,
                    entitySet || entityType,
                    this,
                    generateMockData
                );
                this.stickyEntitySets.push(mockEntitySet as StickyMockEntitySet);
            } else {
                if (this.debug) {
                    this.log.info('Creating entity for ' + (entitySet || entityType)?.name);
                }
                mockEntitySet = new MockDataEntitySet(
                    this.mockDataRootFolder,
                    entitySet || entityType,
                    this,
                    generateMockData
                );
            }
            this.entitySets[entityTypeName] = mockEntitySet;
        }
        return this.entitySets[entityTypeName].readyPromise;
    }

    /**
     * @param odataRequest
     * @param actionData
     */
    public async performAction(odataRequest: ODataRequest, actionData?: object): Promise<any> {
        // if it's a bound action we need to look for the action type
        const rootEntitySet = this.metadata.getEntitySet(odataRequest.query.queryPath[0].path);
        if (rootEntitySet) {
            let currentEntityType = rootEntitySet.entityType;
            let currentEntitySet = rootEntitySet;
            let i = 1;
            for (i = 1; i < odataRequest.query.queryPath.length - 1; i++) {
                const queryPart = odataRequest.query.queryPath[i].path;
                const targetNavProp = currentEntityType.navigationProperties.find(
                    (navProp) => navProp.name === queryPart
                );
                if (targetNavProp) {
                    currentEntityType = targetNavProp.targetType;
                    if (currentEntitySet) {
                        currentEntitySet = currentEntitySet.navigationPropertyBinding[queryPart];
                    }
                }
            }
            const entitySetName = currentEntitySet ? currentEntitySet.name : currentEntityType.name;
            const actionName = odataRequest.query.queryPath[i] ? odataRequest.query.queryPath[i].path : undefined; // Double as action name
            if (actionName && actionName.length > 0) {
                const fqActionName = `${actionName}(${currentEntityType.fullyQualifiedName})`;
                const actionDefinition = this.metadata.getActionByFQN(fqActionName);
                if (actionDefinition) {
                    return (await this.getMockEntitySet(entitySetName)).executeAction(
                        actionDefinition,
                        actionData,
                        odataRequest,
                        odataRequest.query.queryPath[i - 1].keys
                    );
                }
                const collecfqActionName = `${actionName}(Collection(${currentEntityType.fullyQualifiedName}))`;
                const collecactionDefinition = this.metadata.getActionByFQN(collecfqActionName);
                if (collecactionDefinition) {
                    return (await this.getMockEntitySet(entitySetName)).executeAction(
                        collecactionDefinition,
                        actionData,
                        odataRequest,
                        odataRequest.query.queryPath[0].keys
                    );
                }
            }
        } else {
            // Unbound action
            const actionName = odataRequest.query.queryPath[0].path;
            const fqActionName = `${this.metadata.getEntityContainerPath()}/${actionName}()`;
            const actionDefinition = this.metadata.getActionByFQN(fqActionName);
            // Technically i'm not sure what we can do here but we cna at least check if it's a sticky action
            if (actionDefinition) {
                if (this.metadata.getEntitySetByType(actionDefinition.sourceType)) {
                    const targetEntitySet = this.metadata.getEntitySetByType(actionDefinition.sourceType);
                    let outData: any = (await this.getMockEntitySet(targetEntitySet.name)).executeAction(
                        actionDefinition,
                        Object.assign({}, actionData, odataRequest.query.rawParams),
                        odataRequest,
                        odataRequest.query.rawParams
                    );
                    if (this.metadata.getVersion() === '2.0') {
                        const enrichElement = (entitySet: EntitySet, dataLine) => {
                            const keyValues: string[] = [];
                            if (entitySet.entityType.keys.length === 1) {
                                keyValues.push(`${dataLine[entitySet.entityType.keys[0].name]}`);
                            } else {
                                entitySet.entityType.keys.forEach((key) => {
                                    keyValues.push(`${key.name}='${dataLine[key.name]}'`);
                                });
                            }
                            const uri = `${odataRequest.baseUrl}/${entitySet.name}(${keyValues.join(',')})`;
                            dataLine['__metadata'] = {
                                id: uri,
                                uri: uri,
                                type: entitySet.entityTypeName
                            };
                            return dataLine;
                        };

                        // Enrich data with __metadata for v2
                        if (Array.isArray(outData)) {
                            outData = (outData as any[]).map((element) => {
                                return enrichElement(targetEntitySet, element);
                            });
                        } else if (outData != null) {
                            outData = enrichElement(targetEntitySet, outData);
                        }
                    }
                    return outData;
                } else {
                    // There is no entitySet linked to it, handle it in the EntityContainer.js potentially as executeAction
                    const outData = (await MockEntityContainer.read(this.mockDataRootFolder))?.executeAction(
                        actionDefinition,
                        Object.assign({}, actionData, odataRequest.query.rawParams),
                        odataRequest.query.rawParams
                    );
                    return outData;
                }
            } else {
                const fqActionName = `${actionName}()`;
                const actionDefinition = this.metadata.getActionByFQN(fqActionName);
                if (actionDefinition) {
                    for (const entitySet of this.stickyEntitySets) {
                        await entitySet.executeAction(
                            actionDefinition,
                            actionData,
                            odataRequest,
                            odataRequest.query.queryPath[0].keys
                        );
                    }
                    return true;
                }
                return null;
            }
        }
        return null;
    }

    /**
     * @param data
     * @param navPropDetail
     * @param currentEntityType
     * @param currentKeys
     * @param forCreate
     */
    public getNavigationPropertyKeys(
        data: any,
        navPropDetail: any,
        currentEntityType: EntityType,
        currentKeys: Record<string, string>,
        forCreate = false
    ): Record<string, string> {
        if (navPropDetail.referentialConstraint && navPropDetail.referentialConstraint.length > 0) {
            const dataArray = Array.isArray(data) ? data : [data];
            dataArray.forEach((data) => {
                navPropDetail.referentialConstraint.forEach((refConstr) => {
                    currentKeys[refConstr.targetProperty] = data[refConstr.sourceProperty];
                });
                if (data.hasOwnProperty('IsActiveEntity') && navPropDetail.targetType.annotations?.Common?.DraftNode) {
                    currentKeys['IsActiveEntity'] = data.IsActiveEntity;
                }
            });
        } else {
            // Try to find a back link (a nav property going back to the original entityType)
            const originalData = cloneDeep(data);
            const backNav: any = (navPropDetail.targetType as EntityType).navigationProperties.find(
                (targetNavProp) => (targetNavProp as any).targetTypeName === currentEntityType.fullyQualifiedName
            );
            if (backNav && backNav.referentialConstraint && backNav.referentialConstraint.length > 0) {
                backNav.referentialConstraint.forEach((refConstr) => {
                    if (originalData[refConstr.targetProperty] !== undefined) {
                        currentKeys[refConstr.sourceProperty] = originalData[refConstr.targetProperty];
                        delete originalData[refConstr.targetProperty];
                    }
                });
                navPropDetail.targetType.keys.forEach((propKey) => {
                    if (
                        propKey.name === 'IsActiveEntity' &&
                        currentKeys[propKey.name] === undefined &&
                        Object.hasOwnProperty.call(originalData, propKey.name) &&
                        (!forCreate || !propKey.annotations?.Core?.Computed)
                    ) {
                        currentKeys[propKey.name] = originalData[propKey.name];
                        delete originalData[propKey.name];
                    }
                });
            } else if (!this.strictKeyMode) {
                navPropDetail.targetType.keys.forEach((propKey) => {
                    if (
                        Object.hasOwnProperty.call(originalData, propKey.name) &&
                        (!forCreate || !propKey.annotations?.Core?.Computed)
                    ) {
                        currentKeys[propKey.name] = originalData[propKey.name];
                        delete originalData[propKey.name];
                    }
                });
                // If there is no key or only draft stuff
                if (
                    Object.keys(currentKeys).length === 0 ||
                    (Object.keys(currentKeys).length === 1 && currentKeys.hasOwnProperty('IsActiveEntity'))
                ) {
                    // If we still don't have anything, try to get the keys from the current entity that are properties in the target
                    currentEntityType.keys.forEach((propKey) => {
                        if (
                            navPropDetail.targetType.entityProperties.find((prop) => prop.name === propKey.name) &&
                            originalData[propKey.name] !== undefined
                        ) {
                            currentKeys[propKey.name] = originalData[propKey.name];
                            delete originalData[propKey.name];
                        }
                    });
                }
            }
        }
        return currentKeys;
    }

    /**
     * @param currentEntitySet
     * @param entityType
     * @param expandNavProp
     * @param data
     * @param requestExpandObject
     * @param tenantId
     * @param previousEntitySet
     * @param visitedPaths
     */
    async getExpandData(
        currentEntitySet: EntitySet,
        entityType: EntityType,
        expandNavProp: string,
        data: object,
        requestExpandObject: any,
        tenantId: string,
        previousEntitySet: EntitySet,
        visitedPaths: string[]
    ) {
        if (data === null) {
            return;
        }
        const navProp = entityType.navigationProperties.find((navProp) => navProp.name === expandNavProp);
        visitedPaths = visitedPaths.concat();
        visitedPaths.push(expandNavProp);
        let targetEntitySet;
        if (navProp && currentEntitySet && currentEntitySet.navigationPropertyBinding[expandNavProp]) {
            targetEntitySet = currentEntitySet.navigationPropertyBinding[expandNavProp];
        } else if (previousEntitySet && previousEntitySet.navigationPropertyBinding[visitedPaths.join('/')]) {
            targetEntitySet = previousEntitySet.navigationPropertyBinding[visitedPaths.join('/')];
        }
        if (targetEntitySet) {
            const navEntitySet = await this.getMockEntitySet(targetEntitySet.name);
            const dataArray = Array.isArray(data) ? data : [data];
            for (const data of dataArray) {
                const currentKeys = this.getNavigationPropertyKeys(data, navProp, entityType, {});
                if (!navProp.containsTarget) {
                    let expandData = data[expandNavProp];
                    if (!expandData) {
                        expandData = navEntitySet.performGET(currentKeys, navProp.isCollection, tenantId);
                        data[expandNavProp] = expandData;
                    }
                    const expandDetail = requestExpandObject.expand[expandNavProp];
                    if (expandDetail.expand && Object.keys(expandDetail.expand).length > 0) {
                        await Promise.all(
                            Object.keys(expandDetail.expand).map(async (subExpandNavProp) => {
                                return this.getExpandData(
                                    targetEntitySet,
                                    navProp.targetType,
                                    subExpandNavProp,
                                    expandData,
                                    expandDetail,
                                    tenantId,
                                    targetEntitySet,
                                    []
                                );
                            })
                        );
                    }
                }
            }
        } else {
            return data[expandNavProp];
        }
    }

    public getMetadata(): ODataMetadata {
        return this.metadata;
    }

    /**
     * @param odataRequest
     */
    public async getData(odataRequest: ODataRequest, dontClone: boolean = false): Promise<any> {
        if (this.debug) {
            this.log.info('Retrieving data for ' + JSON.stringify(odataRequest.query.queryPath));
        }
        let currentEntitySet: EntitySet = null;
        let previousEntitySet: EntitySet = null;
        let currentEntityType: EntityType = null;
        let visitedPaths: string[] = [];
        let targetContainedData: any;
        let targetContainedEntityType = null;
        const rootEntitySet = this.metadata.getEntitySet(odataRequest.query.queryPath[0].path);
        let isCount = false;
        let data: any = await odataRequest.query.queryPath.reduce(async (data, queryPathPart, index) => {
            data = await data;
            let currentKeys: Record<string, any> = queryPathPart.keys || {};
            let asArray: boolean = Object.keys(currentKeys).length === 0;
            if (queryPathPart.path === '$count') {
                isCount = true;
                return data;
            }
            if ((!currentEntityType || data === null) && index > 0) {
                if (data === null) {
                    if (currentEntityType) {
                        const navPropDetail = currentEntityType.navigationProperties.find(
                            (navProp) => navProp.name === queryPathPart.path
                        );
                        asArray = asArray && navPropDetail.isCollection;
                    }
                    return asArray ? [] : null;
                }
                return data;
            }
            if (!currentEntityType) {
                // First level if entity set, then it's navigation properties
                currentEntitySet = this.metadata.getEntitySet(queryPathPart.path);
                previousEntitySet = currentEntitySet;
                currentEntityType = currentEntitySet.entityType;
            } else {
                const navPropDetail = currentEntityType.navigationProperties.find(
                    (navProp) => navProp.name === queryPathPart.path
                );
                if (navPropDetail.name === 'SiblingEntity' && currentEntityType) {
                    asArray = asArray && navPropDetail.isCollection;
                    currentKeys.IsActiveEntity = !(data as any).IsActiveEntity;
                } else {
                    visitedPaths.push(queryPathPart.path);
                    if (navPropDetail) {
                        if (asArray) {
                            currentKeys = this.getNavigationPropertyKeys(
                                data,
                                navPropDetail,
                                currentEntityType,
                                currentKeys
                            );
                        }
                        const hasOnlyDraftKey =
                            Object.keys(currentKeys).length === 1 && currentKeys.hasOwnProperty('IsActiveEntity');
                        if (
                            navPropDetail.referentialConstraint.length == 0 &&
                            data.hasOwnProperty(queryPathPart.path) &&
                            (this.metadata.getVersion() === '2.0' || hasOnlyDraftKey)
                        ) {
                            // Fake containment for result set
                            targetContainedEntityType = navPropDetail.targetType;
                            targetContainedData = data[queryPathPart.path];
                            currentEntitySet = null;
                            currentEntityType = targetContainedEntityType;
                            if (hasOnlyDraftKey) {
                                currentKeys = {};
                            }
                        } else if (!navPropDetail.containsTarget && previousEntitySet) {
                            currentEntitySet = previousEntitySet.navigationPropertyBinding[visitedPaths.join('/')];
                            previousEntitySet = currentEntitySet;
                            visitedPaths = [];
                            currentEntityType = currentEntitySet.entityType;
                            targetContainedEntityType = null;
                            targetContainedData = null;
                        } else {
                            targetContainedEntityType = navPropDetail.targetType;
                            targetContainedData = data[queryPathPart.path];
                            currentEntitySet = null;
                            currentEntityType = targetContainedEntityType;
                        }
                    } else {
                        currentEntitySet = null;
                        currentEntityType = null;
                    }
                    asArray = asArray && navPropDetail.isCollection;
                }
            }
            if (
                (!currentEntitySet && !targetContainedEntityType) ||
                (this.metadata.getVersion() === '2.0' && targetContainedData)
            ) {
                if (Array.isArray(data)) {
                    return asArray ? [] : null;
                } else {
                    return data[queryPathPart.path];
                }
            }
            return (
                await this.getMockEntitySet(
                    currentEntitySet?.name,
                    undefined,
                    targetContainedEntityType,
                    targetContainedData
                )
            ).performGET(currentKeys, asArray, odataRequest.tenantId, dontClone);
        }, {});

        if (
            rootEntitySet.entityType?.annotations?.Common?.ResultContext?.valueOf() &&
            odataRequest.query.queryPath.length === 1
        ) {
            // Parametrized entityset, they cannot be requested directly
            const potentialTarget = rootEntitySet.entityType.navigationProperties.find(
                (navProp) => navProp.containsTarget
            );
            throw new Error(
                JSON.stringify({
                    message:
                        'Parametrized entityset cannot be queried directly, you need to load the result set, most likely "' +
                        potentialTarget.name +
                        '" in this case'
                })
            );
        }

        if (data !== null || (Array.isArray(data) && data.length > 0)) {
            // Apply $expand
            if (odataRequest.query.expand) {
                await Promise.all(
                    Object.keys(odataRequest.query.expand).map(async (expandNavProp) => {
                        return this.getExpandData(
                            currentEntitySet,
                            currentEntityType,
                            expandNavProp,
                            data,
                            odataRequest.query,
                            odataRequest.tenantId,
                            previousEntitySet,
                            visitedPaths
                        );
                    })
                );
            }

            // Apply $filter
            if ((odataRequest.query.filter || odataRequest.query?.aggregateDefinition?.filter) && Array.isArray(data)) {
                const filterDef = odataRequest.query.filter || odataRequest.query?.aggregateDefinition?.filter;
                const mockEntitySet = await this.getMockEntitySet(
                    currentEntitySet ? currentEntitySet.name : currentEntityType.name
                );
                data = data.filter((dataLine) => {
                    return mockEntitySet.checkFilter(dataLine, filterDef, odataRequest.tenantId);
                });
            }
            // Apply $search
            if (odataRequest.query.searchQuery && Array.isArray(data)) {
                const mockEntitySet = await this.getMockEntitySet(currentEntityType.name);
                data = data.filter((dataLine) => {
                    return mockEntitySet.checkSearch(dataLine, odataRequest.query.searchQuery);
                });
            }

            // Apply $apply for aggregates
            if (odataRequest.query.aggregateDefinition) {
                const dataByGroup = {};
                const mockEntitySet = await this.getMockEntitySet(currentEntityType.name);
                let mockData;
                if (mockEntitySet) {
                    mockData = await mockEntitySet.getMockData(odataRequest.tenantId);
                }

                const getAggregateKey = function (dataLine) {
                    return odataRequest.query.aggregateDefinition.groupBy.reduce((key, groupByProp) => {
                        if (key.length > 0) {
                            key += ',';
                        }
                        key += dataLine[groupByProp];
                        return key;
                    }, '');
                };
                data.forEach((dataLine) => {
                    const aggregateKey = getAggregateKey(dataLine);
                    if (!dataByGroup[aggregateKey]) {
                        dataByGroup[aggregateKey] = [];
                    }
                    dataByGroup[aggregateKey].push(dataLine);
                });

                data = Object.keys(dataByGroup).map((groupName) => {
                    const dataToAggregate = dataByGroup[groupName];
                    const outData = {};
                    odataRequest.query.aggregateDefinition.groupBy.forEach((propName) => {
                        outData[propName] = dataToAggregate[0][propName];
                    });
                    odataRequest.query.aggregateDefinition.aggregates.forEach((aggregateDefinition) => {
                        let propValue;
                        if (
                            aggregateDefinition.operator === undefined &&
                            mockData &&
                            mockData.hasCustomAggregate(aggregateDefinition.name)
                        ) {
                            propValue = mockData.performCustomAggregate(aggregateDefinition.name, dataToAggregate);
                        } else {
                            dataToAggregate.forEach((dataLine) => {
                                const currentValue = dataLine[aggregateDefinition.sourceProperty];
                                if (propValue === undefined) {
                                    propValue = currentValue;
                                } else {
                                    switch (aggregateDefinition.operator) {
                                        case 'max':
                                            propValue = Math.max(propValue, currentValue);
                                            break;
                                        case 'min':
                                            propValue = Math.min(propValue, currentValue);
                                            break;
                                        case 'average':
                                            propValue += currentValue;
                                            break;
                                        default:
                                            propValue += currentValue;
                                            break;
                                    }
                                }
                            });
                        }
                        if (aggregateDefinition.operator === 'average') {
                            propValue = propValue / dataToAggregate.length;
                        }
                        outData[aggregateDefinition.name] = propValue;
                    });
                    return outData;
                });
            }
            // Apply $orderby
            if (odataRequest?.query?.orderBy && odataRequest?.query?.orderBy.length > 0) {
                data.sort(function (firstElement, secondElement) {
                    let isDecisive = false;
                    let outValue = 0;
                    odataRequest.query.orderBy.forEach((orderByDef) => {
                        if (isDecisive) {
                            return;
                        }
                        if (firstElement[orderByDef.name] > secondElement[orderByDef.name]) {
                            outValue = orderByDef.direction === 'asc' ? 1 : -1;
                            isDecisive = true;
                        } else if (firstElement[orderByDef.name] < secondElement[orderByDef.name]) {
                            outValue = orderByDef.direction === 'asc' ? -1 : 1;
                            isDecisive = true;
                        }
                    });
                    return outValue;
                });
            }
            // Apply $select
            const originalData = cloneDeep(data);
            if (odataRequest?.query?.properties) {
                if (odataRequest.query.properties['DraftAdministrativeData']) {
                    if (Array.isArray(data)) {
                        data = (data as any[]).map((element) => {
                            if (!element.DraftAdministrativeData) {
                                element.DraftAdministrativeData = null;
                            }
                            return element;
                        });
                    } else if (data != null && data.constructor.name === 'Object') {
                        if (!data.DraftAdministrativeData) {
                            data.DraftAdministrativeData = null;
                        }
                    }
                }
            }
            if (odataRequest?.query?.properties && Object.keys(odataRequest?.query?.properties).length > 0) {
                const apply$Select = (
                    expand: { expand: any; select: { [name: string]: boolean } },
                    data: { [name: string]: any }[]
                ) => {
                    const selectedPropertyNames = Object.keys(expand.select);
                    const expandedNavProps = Object.keys(expand.expand);
                    const allNavProps = currentEntityType?.navigationProperties.map((navProp) => navProp.name) || [];
                    data.forEach((element) => {
                        // the element might be null (if it is a 1:1 navigation property)
                        if (element === null) {
                            return;
                        }

                        // if all properties are requested ("*") keep everything else delete unwanted properties
                        if (!selectedPropertyNames.includes('*')) {
                            const propertyNames = Object.keys(element);
                            const differenceKeys = propertyNames.filter(
                                (propertyName) => !selectedPropertyNames.includes(propertyName)
                            );
                            differenceKeys.forEach((k) => {
                                delete element[k];
                            });
                        }

                        expandedNavProps.forEach((navProp) => {
                            const expandedIndex = allNavProps.indexOf(navProp);
                            if (expandedIndex !== -1) {
                                allNavProps.splice(expandedIndex, 1);
                            }
                            const subElement = element[navProp];
                            if (Array.isArray(subElement)) {
                                apply$Select(expand.expand[navProp], subElement);
                            } else {
                                apply$Select(expand.expand[navProp], [subElement]);
                            }
                        });

                        if (selectedPropertyNames.includes('*') && currentEntitySet && allNavProps.length > 0) {
                            // we may still want to remove navProp that were inline and not requested
                            allNavProps.forEach((k) => {
                                delete element[k];
                            });
                        }
                    });
                };

                const select = {};
                Object.keys(odataRequest.query.properties).forEach((prop) => (select[prop] = true));
                const expand = {
                    expand: odataRequest.query.expand ?? {},
                    select: select
                };

                if (Array.isArray(data)) {
                    apply$Select(expand, data);
                } else if (data != null && data.constructor.name === 'Object') {
                    apply$Select(expand, [data]);
                }
            }
            odataRequest.dataLength = (Array.isArray(data) && data.length) || 1;

            // Apply $skip / $top
            if (Array.isArray(data) && odataRequest.query.startIndex !== undefined && odataRequest.query.maxElements) {
                data = (data as any[]).slice(
                    odataRequest.query.startIndex,
                    odataRequest.query.startIndex + odataRequest.query.maxElements
                );
            }

            if (this.metadata.getVersion() === '2.0') {
                const enrichElement = (
                    entitySet: EntitySet | undefined,
                    entityType: EntityType | undefined,
                    dataLine,
                    originalDataLine
                ) => {
                    if (!entityType) {
                        return;
                    }
                    const keyValues: string[] = [];
                    if (entityType.keys.length === 1) {
                        const key = entityType.keys[0];
                        keyValues.push(`${dataLine[key.name] || originalDataLine[key.name]}`);
                    } else {
                        entityType.keys.forEach((key) => {
                            keyValues.push(`${key.name}='${dataLine[key.name] || originalDataLine[key.name]}'`);
                        });
                    }

                    const uri = `${odataRequest.baseUrl}/${
                        entitySet ? entitySet.name : entityType.name
                    }(${keyValues.join(',')})`;
                    dataLine['__metadata'] = {
                        id: uri,
                        uri: uri,
                        type: entityType.fullyQualifiedName
                    };

                    entityType.navigationProperties.forEach((navProp) => {
                        //eslint-disable-next-line
                        if (dataLine.hasOwnProperty(navProp.name)) {
                            if (entitySet.navigationPropertyBinding[navProp.name]) {
                                if (navProp.isCollection) {
                                    let navPropData = dataLine[navProp.name];
                                    let hasResult = false;
                                    if (!Array.isArray(navPropData) && navPropData.hasOwnProperty('results')) {
                                        navPropData = navPropData.results;
                                        hasResult = true;
                                    }
                                    if (!Array.isArray(navPropData) && navPropData.hasOwnProperty('__deferred')) {
                                        navPropData = [];
                                        hasResult = false;
                                    }
                                    dataLine[navProp.name] = {
                                        results: navPropData.map((element, idx) => {
                                            let originalData = originalDataLine[navProp.name][idx];
                                            if (hasResult) {
                                                originalData = originalDataLine[navProp.name].results[idx];
                                            }
                                            return enrichElement(
                                                entitySet.navigationPropertyBinding[navProp.name],
                                                entityType.navigationProperties.find((nav) => nav.name === navProp.name)
                                                    ?.targetType,
                                                element,
                                                originalData
                                            );
                                        })
                                    };
                                } else if (dataLine[navProp.name] !== null) {
                                    dataLine[navProp.name] = enrichElement(
                                        entitySet.navigationPropertyBinding[navProp.name],
                                        entityType.navigationProperties.find((nav) => nav.name === navProp.name)
                                            ?.targetType,
                                        dataLine[navProp.name],
                                        originalDataLine[navProp.name]
                                    );
                                }
                            }
                        }
                    });
                    return dataLine;
                };

                // Enrich data with __metadata for v2
                if (Array.isArray(data)) {
                    data = (data as any[]).map((element, idx) => {
                        return enrichElement(currentEntitySet, currentEntityType, element, originalData[idx]);
                    });
                } else if (data != null) {
                    data = enrichElement(currentEntitySet, currentEntityType, data, originalData);
                }
            }

            if (isCount) {
                data = odataRequest.dataLength;
            }
        }

        return data;
    }

    /**
     * @param odataRequest
     * @param patchData
     */
    public async updateData(odataRequest: ODataRequest, patchData: any) {
        const entitySetName = odataRequest.query.queryPath[0].path;
        return await (
            await this.getMockEntitySet(entitySetName)
        ).performPATCH(odataRequest.query.queryPath[0].keys, patchData, odataRequest.tenantId, true);
    }

    /**
     * @param odataRequest
     * @param postData
     */
    public async createData(odataRequest: ODataRequest, postData: any) {
        const entitySetName = odataRequest.query.queryPath[0].path;
        const navPropertyName =
            odataRequest.query.queryPath.length > 1 ? odataRequest.query.queryPath[1].path : undefined;
        const entitySet = this.metadata.getEntitySet(entitySetName);
        if (navPropertyName) {
            // Creating a sub object
            const entityType = entitySet.entityType;
            const navPropDetail = entityType.navigationProperties.find(
                (navProp) => navProp.name === navPropertyName
            ) as any;
            const navPropEntityType = (navPropDetail as any).targetType;
            const data = (await this.getMockEntitySet(entitySetName)).performGET(
                odataRequest.query.queryPath[0].keys,
                false,
                odataRequest.tenantId,
                true
            );

            const providedKeys = {};
            navPropEntityType.keys.forEach((key) => {
                if (postData[key.name] !== undefined) {
                    providedKeys[key.name] = postData[key.name];
                }
            });
            const currentKeys = this.getNavigationPropertyKeys(
                data,
                navPropDetail,
                entitySet.entityType,
                providedKeys,
                true
            );
            if (data.DraftAdministrativeData !== null && data.DraftAdministrativeData !== undefined) {
                data.DraftAdministrativeData.LastChangeDateTime = _getDateTimeOffset(this.isV4());
            }
            if (!navPropDetail.containsTarget) {
                const targetEntitySet = entitySet.navigationPropertyBinding[navPropertyName];
                (odataRequest as ODataV4Request).context = `../$metadata#${targetEntitySet.name}/$entity`;
                (odataRequest as ODataV4Request).location = `${targetEntitySet.name}(${Object.keys(currentKeys)
                    .map((key) => `${key}='${currentKeys[key]}'`)
                    .join(',')})`;
                postData = (await this.getMockEntitySet(targetEntitySet.name)).performPOST(
                    currentKeys,
                    postData,
                    odataRequest.tenantId,
                    true
                );
            } else {
                if (!data[navPropertyName]) {
                    data[navPropertyName] = [];
                }
                data[navPropertyName].push(postData);
            }
            return postData;
        } else {
            // Creating a main object
            const currentKeys = {};
            entitySet.entityType.keys.forEach((key) => {
                if (postData[key.name] !== undefined) {
                    currentKeys[key.name] = postData[key.name];
                }
            });
            postData = (await this.getMockEntitySet(entitySet.name)).performPOST(
                currentKeys,
                postData,
                odataRequest.tenantId,
                true
            );
            (odataRequest as ODataV4Request).context = `../$metadata#${entitySet.name}/$entity`;
            (odataRequest as ODataV4Request).location = `${entitySet.name}(${Object.keys(currentKeys)
                .map((key) => `${key}='${currentKeys[key]}'`)
                .join(',')})`;
            return postData;
            // this.getMockEntitySet(entitySetName).performPOST(postData, res);
        }
    }

    /**
     * @param odataRequest
     */
    public async deleteData(odataRequest: ODataRequest) {
        const entitySetName = odataRequest.query.queryPath[0].path;
        const mockEntitySet = await this.getMockEntitySet(entitySetName);
        const deleteResult = mockEntitySet.performDELETE(
            odataRequest.query.queryPath[0].keys,
            odataRequest.tenantId,
            true
        );
        return deleteResult;
    }
    public getParentEntityKeys(keyValues: KeyDefinitions, entitySetDefinition: EntitySet) {
        return undefined;
    }
    public async getDraftRoot(keyValues: KeyDefinitions, tenantId: string, entitySetDefinition: EntitySet) {
        let data = {};
        try {
            const rootInfo = this.metadata.resolveDraftRoot(entitySetDefinition);
            let parentRequestPath = `${entitySetDefinition.name}(${Object.keys(keyValues)
                .map((keyName) => `${keyName}=${keyValues[keyName]}`)
                .join(',')})`;
            for (let i = 0; i < rootInfo.path.length; i++) {
                parentRequestPath += '/';
                parentRequestPath += rootInfo.path[i].partner.name;
            }
            const parentRequest = new ODataV4Request('', parentRequestPath, this.metadata);

            data = await this.getData(parentRequest, true);
        } catch (e) {
            console.log("Couldn't find the parent for " + keyValues);
        }

        return data;
    }

    /**
     * @param res
     * @param tenantId
     */
    public resetStickySessionTimeout(res: any, tenantId: string) {
        res.setHeader('odata-version', '4.0');
        let UUID = '';
        let timeoutTime = 20;
        this.stickyEntitySets.forEach((entitySet) => {
            UUID = entitySet.resetSessionTimeout(tenantId);
            timeoutTime = entitySet.sessionTimeoutTime;
        });
        res.setHeader('sap-contextid', UUID);
        res.setHeader('sap-http-session-timeout', timeoutTime.toString());
        res.statusCode = 200;
        res.end();
    }
}
