import { EntitySet, EntityType, Property } from '@sap-ux/annotation-converter';
import { ODataMetadata } from './metadata';
import { FileBasedMockData, KeyDefinitions } from './fileBasedMockData';

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface ODataRequestInterface {}
export interface EntitySetInterface {
    checkKeyValue(mockData: object, keyValues: object, keyName: string, keyProp?: Property): boolean;
    getParentEntityInterface(tenantId: string): Promise<FileBasedMockData | undefined>;
    getEntityInterface(entitySetName: string, tenantId: string): Promise<FileBasedMockData | undefined>;
    getMockData(tenantId: string): FileBasedMockData;
    isV4(): boolean;
}
export interface DataAccessInterface {
    isV4(): boolean;
    getNavigationPropertyKeys(
        data: any,
        navPropDetail: any,
        currentEntityType: EntityType,
        currentKeys: Record<string, string>,
        forCreate?: boolean
    ): Record<string, string>;
    getMockEntitySet(
        entityTypeName,
        generateMockData?: boolean,
        containedEntityType?: EntityType,
        containedData?: any
    ): Promise<EntitySetInterface>;
    getData(odataRequest: ODataRequestInterface): Promise<any>;
    getDraftRoot(keyValues: KeyDefinitions, tenantId: string, entitySetDefinition: EntitySet);
    getParentEntityKeys(keyValues: KeyDefinitions, entitySetDefinition: EntitySet);
    getMetadata(): ODataMetadata;
    debug: boolean;
    log;
}

/**
 *
 */
export class ExecutionError extends Error {
    statusCode: number;
    messageData: object;
    isSAPMessage: boolean;
    isCustomError = true;
    headers: Record<string, string>;

    /**
     * @param message
     * @param statusCode
     * @param messageData
     * @param isSAPMessage
     */
    constructor(
        message: string,
        statusCode: number,
        messageData: object,
        isSAPMessage: boolean,
        headers: Record<string, string> = {}
    ) {
        super(message);
        this.statusCode = statusCode;
        this.messageData = messageData;
        this.isSAPMessage = isSAPMessage;
        this.headers = headers;
    }
}

export function _getDateTimeOffset(isV4: boolean) {
    const date = new Date();
    return isV4 ? date.toISOString() : '/Date(' + date.getTime() + '+0000)/';
}
