import { Request, Response } from 'express';
import { RequestHandler } from 'express-serve-static-core';
import { createBoundary } from './keyGenerator';
import { createRequest } from './http/simpleHttpRequest';
import { getBoundary, convertBatch } from './content/batchParser';
import { ODataMetadata } from '../data/metadata';
import { DataAccess } from '../data/dataAccess';
import { ODataRequest } from '../request/odataRequest';
import { ODataV2Request } from '../request/odataV2Request';
import { ODataV4Request } from '../request/odataV4Request';
import { ServiceConfig } from '../../api';
import * as http from 'http';

/**
 * Simplified/mocked implementation of a batch context required by the
 * xs2/xsodata lib
 */
let index = 0;
const mockContext = {
    getNextKeyCounter(): number {
        return index++;
    }
};

/**
 * Write batch content to the response. The method has been copied from the
 * xs2/xsodata project and modified because FE expects status code 202.
 *
 * @param batch
 * @param context
 * @param response
 */
function writeBatch(batch, context, response: http.ServerResponse, innerPart: boolean = false): void {
    response.statusCode = 200;
    const boundary = 'batch_' + createBoundary(context);
    if (innerPart) {
        response.write('Content-Type: multipart/mixed; boundary=' + boundary);
    } else {
        response.setHeader('Content-Type', 'multipart/mixed; boundary=' + boundary);
    }

    response.write('--' + boundary + '\r\n');
    for (let i = 0; i < batch.parts.length; i++) {
        if (i !== 0) {
            response.write('\r\n--' + boundary + '\r\n');
        }
        if (batch.parts[i].isChangeSet) {
            writeBatch(batch.parts[i], context, response, true);
        } else {
            batch.parts[i].write(context, response);
        }
    }
    response.write('\r\n--' + boundary + '--\r\n');
}

/**
 * @param metadata
 * @param dataAccess
 * @param config
 */
export function batchRequestHandler(
    metadata: ODataMetadata,
    dataAccess: DataAccess,
    config: ServiceConfig
): RequestHandler {
    return (req: Request, res: Response): void => {
        const request = createRequest(req, '/');
        if (metadata.getVersion() === '2.0') {
            res.setHeader('dataserviceversion', metadata.getVersion());
        } else {
            res.setHeader('odata-version', metadata.getVersion());
        }
        res.statusCode = 200;
        const boundary = getBoundary(req.headers['content-type']);

        /**
         * Process the current part and prepare the return part
         *
         * @param part
         */
        async function handlePart(part: any): Promise<void> {
            const baseUrl = req.baseUrl.replace('/$batch', '');
            if (config.debug) {
                console.log(part.rawData.url);
            }
            const minimalRepresentation =
                part.rawData.headers['prefer'] && part.rawData.headers['prefer'] === 'return=minimal';
            const [method, url] = part.rawData.url.split(' ');
            const odataReq: ODataRequest =
                metadata.getVersion() === '2.0'
                    ? new ODataV2Request(baseUrl, url, metadata)
                    : new ODataV4Request(baseUrl, url, metadata, minimalRepresentation);
            try {
                switch (method) {
                    case 'PATCH':
                    case 'MERGE': {
                        const patchData = part.rawData.payload[0].length ? JSON.parse(part.rawData.payload[0]) : {};
                        const updatedData = await dataAccess.updateData(odataReq, patchData);
                        part.response = odataReq.createResponse(updatedData);

                        break;
                    }
                    case 'DELETE':
                        await dataAccess.deleteData(odataReq);
                        part.response = odataReq.createResponse(null);
                        break;
                    case 'POST': {
                        const postData = part.rawData.payload[0].length ? JSON.parse(part.rawData.payload[0]) : {};
                        const actionResponse = await dataAccess.performAction(odataReq, postData);
                        if (actionResponse === null) {
                            const createdData = await dataAccess.createData(odataReq, postData);
                            part.response = odataReq.createResponse(createdData, true, createdData);
                        } else {
                            part.response = odataReq.createResponse(actionResponse);
                        }
                        break;
                    }
                    case 'GET':
                    default: {
                        const actionResponse = await dataAccess.performAction(odataReq);
                        if (actionResponse === null) {
                            const data = await dataAccess.getData(odataReq);
                            part.response = odataReq.createResponse(data);
                        } else {
                            part.response = odataReq.createResponse(actionResponse);
                        }
                        break;
                    }
                }
            } catch (e) {
                part.response = ODataRequest.createErrorResponse(e);
            } finally {
                odataReq.applyResponseHeaders(res);
            }
        }

        request.getBodyAsString(async (body) => {
            const batch = convertBatch(body, boundary);
            for (const part of batch.parts) {
                if (part.isChangeSet) {
                    await Promise.all(part.parts.map(async (subPart) => await handlePart(subPart)));
                } else {
                    await handlePart(part);
                }
            }
            writeBatch(batch, mockContext, res);
            res.end();
        });
    };
}
